<template>
    <div class="singer-detail">
        <div class="header">
            <a class="back" href="javascript:" @click="goBack">
                <i></i>
            </a>
            <h1 v-text="singerName"></h1>
        </div>
        <div class="con">
            <div class="thumb">
                <img :src="'https://y.gtimg.cn/music/photo_new/T001R300x300M000'+ singerMid +'.jpg?max_age=2592000'" alt="">
            </div>

            <div class="song-list">
                <div id="songWrapper" :style="autoPos">
                    <ul>
                        <li v-for="(song) in songList" :key="song.musicData.songmid" @click="addPlayList(song)">
                            <h3 v-text="song.musicData.songname"></h3>
                            <p>{{singerName}}.<span v-text="song.musicData.albumname"></span></p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// 载入接口
import API from '@/api/api'
import HTTP from '@/utils/http'

// 载入iscroll滚动插件
import IScroll from 'iscroll/build/iscroll-probe'

import {mapGetters, mapMutations} from 'vuex'
export default {
    data: function() {
        return {
            singerMid: '',
            songList: [],
            singerName: '',
            sigDetlIScroll: null
        }
    },
    computed: {
        ...mapGetters({
            curSong: 'getCurSong'
        }),
        // 是否有miniplayer在显示，动态调整滚动区域的位置(bottom值)
        autoPos: function() {
            return this.curSong ? 'bottom: 60px' : 'bottom: 0'
        }
    },
    created() {
        this.singerMid = this.$route.params.singer_mid
        this.getSingerData()
    },
    mounted() {
        this.sigDetlIScroll = new IScroll('#songWrapper', {
            mouseWheel: true,
            useTransform: true,
            useTransition: true,
            disableMouse: true,
            disablePointer: true,
            disableTouch: false,
            tap: false,
            click: true,
            probeType: 2
        })
    },
    methods: {
        ...mapMutations([
            'ADD_TO_SONG_LIST'
        ]),
        // 添加歌曲部分信息(此时缺少播放地址)到播放列表
        addPlayList(song) {
            let albummid = song.musicData.albummid
            let songmid = song.musicData.songmid
            let songname = song.musicData.songname
            let singer = song.musicData.singer
            this.ADD_TO_SONG_LIST({albummid, songmid, songname, singer})
            
            this.$router.push({name: "play", params: {song_mid: songmid}});
        },
        getSingerData() {
            let url = `${API.singer.singerDetail}${this.singerMid}`
            HTTP.get(url, {param: 'jsonpCallback'})
            .then(({code, data: {singer_name, list}}) => {
                this.singerName = singer_name
                this.songList = list

                setTimeout(() => {
                    this.sigDetlIScroll.refresh()
                }, 0)
            })
        },
        goBack() {
            this.$router.go(-1)
        }
    },
    components: {
    }
}
</script>
<style scoped lang="scss">
@import 'SCSS/singerDetail';
</style>